<template>
  <div id="welcome">
    <el-card>
      <el-container>
        <el-header height="100px">
          <h1 style="text-align:center">欢迎您!{{name}}</h1>
          <div class="box">
            <div class="left">
            <span>待审核:</span> <span>{{uncheceked}}</span>
            </div>
            <div class="right">
              <span>已通过:</span> <span>{{pass}}</span>
            </div>
          </div>
        </el-header>
        <el-container>
          <el-aside width="400px">
            <div id="pie" :style="{width: '400px', height: '400px'}"></div>
          </el-aside>
          <el-main>
            <a href="#">最近新消息>></a>
            <div id="box">
              <ul id="animate"  :class="{ anim: animate == true }">
                  <li v-for='(item, index) in items' :key="index">{{item.importTime}}{{item.departmentName}}{{item.importerName}}上传了{{item.systemName}}的等保信息</li>
              </ul>
            </div>
          </el-main>
        </el-container>
      </el-container>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      name: '',
      uncheceked: '',
      pass: '',
      animate: false,
      items: []
    }
  },
  created () {
    // 获取用户名
    this.$get('/api/v1/user/' + window.sessionStorage.getItem('userid'))
      .then(response => {
        this.name = response.data.data.name
      })
    this.drawPie()
    // 轮播数据
    this.$get('/api/v1/sysInfo/carouselData')
      .then(response => {
        this.items = response.data.data
      })
    // 待审核 已通过
    this.$get('/api/v1/sysInfo/countByAuthority')
      .then(response => {
        this.uncheceked = response.data.data[0]
        this.pass = response.data.data[1]
      })
    // 轮播动画
    setInterval(this.scroll, 2000)
  },
  methods: {
    // 饼状图
    drawPie () {
      this.$get('/api/v1/sysInfo/groupGradeNum')
        .then(response => {
          const gradenum = response.data.data
          // 为charts选定父容器并初始化charts画布
          const myChart = this.$echarts.init(document.getElementById('pie'))
          // 为图表添加数据
          myChart.setOption({
            title: {
              text: '集团的各级系统占比',
              left: 'center' // 标题在图表中间
            },
            tooltip: {
              trigger: 'item',
              formatter: '{b} : ({d}%)'
            },
            legend: {
              bottom: 10,
              left: 'center', // 底部注释的位置
              data: ['二级', '三级', '四级']
            },
            series: [
              {
                type: 'pie',
                radius: '65%',
                center: ['50%', '50%'],
                selectedMode: 'single',
                data: [
                  { value: gradenum.grade2num, name: '二级' },
                  { value: gradenum.grade3num, name: '三级' },
                  { value: gradenum.grade4num, name: '四级' }
                ],
                emphasis: {
                  itemStyle: {
                    shadowBlur: 10,
                    shadowOffsetX: 0,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                  }
                }
              }
            ]
          })
        })
        .catch(err => err)
    },
    // 动态消息
    scroll () {
      this.animate = true
      setTimeout(() => {
        this.items.push(this.items[0])
        this.items.shift()
        this.animate = false
      }, 500)
    }
  }
}
</script>

<style lang="less" scoped>
.el-card {
  background-color: gainsboro;
}
.box {
  display: flex;
  width: 600px;
  margin: 0 auto;
}
.left{
  width: 300px;
  flex: 1;
}
// 消息动态显示
#box{
  width: 640px;
  overflow: hidden;
  padding-left: 10px;
}
.anim{
  transition: all 0.5s;
  margin-top: -30px;
}
#animate li{
  list-style: none;
  line-height: 30px;
  height: 30px;
}

</style>
